import React, { Component } from 'react';
import Types from 'prop-types';
import { formatDate } from '@transferwise/formatting';

import Header from '../header';
import DayCalendarTable from './table';

class DayCalendar extends Component {
  static propTypes = {
    selectedDate: Types.instanceOf(Date),
    min: Types.instanceOf(Date),
    max: Types.instanceOf(Date),
    month: Types.number.isRequired,
    year: Types.number.isRequired,
    locale: Types.string.isRequired,
    shortDate: Types.bool.isRequired,
    onSelect: Types.func.isRequired,
    onLabelClick: Types.func.isRequired,
    onViewDateUpdate: Types.func.isRequired,
  };

  static defaultProps = {
    selectedDate: null,
    min: null,
    max: null,
  };

  selectPreviousMonth = () => {
    const { month, year } = this.props;
    this.props.onViewDateUpdate({
      month: month <= 0 ? 11 : month - 1,
      year: month <= 0 ? year - 1 : year,
    });
  };

  selectNextMonth = () => {
    const { month, year } = this.props;
    this.props.onViewDateUpdate({
      month: month >= 11 ? 0 : month + 1,
      year: month >= 11 ? year + 1 : year,
    });
  };

  render() {
    const {
      selectedDate,
      min,
      max,
      month,
      year,
      locale,
      shortDate,
      onLabelClick,
      onSelect,
    } = this.props;
    return (
      <div>
        <Header
          label={formatDate(new Date(year, month), locale, {
            month: shortDate ? 'short' : 'long',
            year: 'numeric',
          })}
          onLabelClick={onLabelClick}
          onPreviousClick={this.selectPreviousMonth}
          onNextClick={this.selectNextMonth}
        />
        <DayCalendarTable {...{ selectedDate, min, max, month, year, locale, onSelect }} />
      </div>
    );
  }
}

export default DayCalendar;



